.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2em;
}

a {
  color: #61dafb;
  text-decoration: underline;
}

.Stage {
  width: 640px;
  height: 360px;
  position: relative;
}

.App .Stage .vjs-big-play-button {
  margin-left: 40%;
  margin-top: 20%;
  width: 20%;
  box-shadow: 0px 0px 10px #888888;
}

.Spot {
  margin: 5px;
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 20px;
  background-color: #02071fb3;
  border-radius: 5px;
  border: solid 3px white;
}

p.LinkSpot1 {
  top: 4px;
}

p.LinkSpot2 {
  top: 94px;
}

#bs1 {
  left: 146px;
  margin-top: 13%;
  padding-top: 5px;
}

#ds {
  margin-left: 143px;
  margin-top: 75px;
}

#spongebob {
  height: 160px;
  width: 160px;
}

#nyancat {
  height: 160px;
  width: 160px;
}


#Time {
  font-size: 0.5em;
  color: #2d2d2d;
}
